@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.UsersList, .UsersList__empty, .UsersList__error, .UsersList__loading {
    height: calc(10 * var(--item-height)); /* assigning height keeps the layout from shifting */
}

.UsersList {
    overflow: auto; /* in case the content is longer  */
    counter-reset: user-counter;
}

.UsersList__empty, .UsersList__error {
    text-align: center;
    color: var(--error-color);
    font-size: 24px;
}

.UsersList__loading {
    text-align: center;
}

.UsersList__item {
    display: flex;
    border-bottom: 1px solid var(--underline-color); /* border to visually center the content even if the user names are shorter */
    height: var(--item-height);
    counter-increment: user-counter;
    align-items: flex-end;
}

.UsersList__item::before {
    width: calc(var(--spacer) * 6); /* enough space to keep 3 numbers */
    content: counter(user-counter) '.' ;
    padding-right: calc(var(--spacer) * 2);
    color: var(--font-color-secondary);
}

.UsersList__name {
    flex: 1 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.UsersList__tag {
    color: var(--font-color-secondary);
    margin-left: auto;
}

.SearchBar {
    border: 1px solid var(--border-color);
    outline: var(--border-color-transparent) solid 3px;
    border-radius: 3px;
    font-size: 16px;
    transition: outline 0.4s ease-in-out;
}

.SearchBar:focus {
    outline: var(--border-color) solid 3px;
}

:root {
  --bg-color: #FFF4EC;
  --border-color: #FFDAC1;
  --border-color-transparent: #ffdac100;
  --underline-color: #FFCACB;
  --error-color: #FF9AA2;
  --font-color-primary: #363742;
  --font-color-secondary: #957DAD;

  --spacer: 8px;
  --item-height: 40px;
}

body {
  display: flex;
  flex-direction: column;
  background-color: #FFF4EC;
  background-color: var(--bg-color);
  font-size: 16px;
  color: #363742;
  color: var(--font-color-primary);
  align-items: center;
}

/* treat App as a wrapper to position the users list while keeping it flexible */
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch; /* make items stretch to take the full width so the content won't jump on filter */
  height: 100vh;
  min-width: 500px;
}

/* depends on the target devices, this should work properly on iPhone SE */
@media screen and (max-width: 500px) {
  .App {
    min-width: 300px;
    max-width: 300px;
  }
}

.App__title {
  font-size: 24px;
  font-weight: bold;
  padding-bottom: calc(8px * 2);
  padding-bottom: calc(var(--spacer) * 2);
  text-align: center;
}

.App__searchBar {
  margin-bottom: 8px;
  margin-bottom: var(--spacer);
  padding: calc(8px / 2);
  padding: calc(var(--spacer) / 2);
}

